	module.exports = {"data":{"markdownRemark":{"html":"<h2>Self Organization</h2>\n<p>Scrum teams are said to be self organising. This means that the work done and generally the way it is done is left up to the team to decide.</p>\n<p>During Sprint Planning the team will look at the product backlog (which will have been prioritised by the product owner) and decide which stories the will work on in the upcoming sprint and the tasks which will need completing to mark the story as Done. The team will estimate a score or size for the story to denote effort. </p>\n<p>During a sprint it is up to the members of the team to pick up sprint backlog stories to work on, generally they will pick ones they think they can achieve. Due to it being a self organising team, there should be no assigning a task to a team member by someone else.</p>","fields":{"slug":"/agile/self-organization/"},"frontmatter":{"title":"Self Organization"}}},"pathContext":{"slug":"/agile/self-organization/"}}

